.App {
  text-align: center;
  background-color: lightgray;
  font-size: calc(10px + 2vmin);
  color: black;
}

.Container {
  min-height: 100vh;
  min-width: 500px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  column-gap: 3em;
  font-size: 1em;
}

.Loading {
  min-height: 100vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.CTA {
  display: flex;
  flex-direction: column;
  align-items: center;
  /*gap: 1em;*/
  max-width: 500px;
  border-style: solid;
  border-color: gray;
}

.CTA p {
  margin: 0.5em;
}

.CTA .Header {
  font-weight: bold;
  font-size: 1.2em;
}

.CTA .Sub-Text {
  font-size: 0.8em;
  font-style: italic;
}

.Button {
  font-weight: bold;
  background-color: deepskyblue;
  border-style: solid;
  border-color: dodgerblue;
  box-shadow: 6px 6px 0px 0px rgba(34, 60, 80, 0.2);
  margin: 1em;
  padding: 0.7em 1.4em 0.7em 1.2em;
}


