.Button a {
  width: 100%;
}


.Button:hover {
  background-color: lightsalmon;
  border-color: darksalmon;
  cursor: pointer;
}

.Image {
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: center;
  width: 400px;
  height: 400px;
  border-style: solid;
  border-color: gray;
}

.Popup {
  background-color: white;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 400px;
  height: 300px;
  position: fixed;
  border-style: solid;
  border-color: gray;
  box-shadow: 8px 8px 0px 0px rgba(34, 60, 80, 0.2);
}